import React, { Fragment, useState, useEffect } from "react";
import { FormControl, Grid, TextField, withStyles, InputLabel, Select, MenuItem, Button, FormHelperText } from "@material-ui/core";
import { useForm } from "../../useForm";
import { connect } from "react-redux";
//import * as actions from "../../../../../actions/Staff/addStaff";
import * as DActions from "../../../../../actions/Deposit/deposit";
//import * as userLevelActions from "../../../../../actions/UserLevel/userLevel";
import { ToastConsumer, useToasts } from "react-toast-notifications";
import "../../staffForm.css";
import { RestoreSharp } from "@material-ui/icons";
import * as categActions from "../../../../../actions/Category/categories";


const styles = theme => ({
    root: {
        '& .MuiTextField-root': {
            margin: theme.spacing(1),
            minWidth: 230,
        }
    },

    formControl: {
        margin: theme.spacing(1),
        minWidth: 230,
    },
    smMargin: {
        margin: theme.spacing(1),

    },
})

const initialFieldValues = {

    name: '',
    description: '',
    amount: '',
    // phone: '',
    // location: '',
    // // email: '',
    // // address: '',
    // companyDataId: '',


}






const AddDepositForm = ({ classes, ...props }) => {

    //toast message
    const { addToast } = useToasts()

    //realtime validation for the whole form controls
    const validate = (fieldValues = values) => {
        let temp = { ...errors }
        if ('name' in fieldValues) {
            temp.name = fieldValues.name ? "" : "This field is required"
            // addToast("User data saved succeffuly", {appearance:'success'})
            // console.log("Tast has beeen displayed")
        }


        if ('description' in fieldValues) {
            temp.description = fieldValues.description ? "" : "This field is required"
        }
        if ('amount' in fieldValues) {
            temp.amount = fieldValues.amount ? "" : "This field is required"
        }



        // if ('location' in fieldValues) {
        //     temp.location = fieldValues.location ? "" : "This field is required"
        // }


        // if ('email' in fieldValues) {
        //     if (temp.email != " ") {
        //         temp.email = (/^&|.+@.+..+/).test(fieldValues.email) ? "" : "Email is not valid"
        //     }
        // }
        // if ('phone' in fieldValues) {
        //     temp.phone = fieldValues.phone ? "" : "This field is required"
        // }
        // if ('companyDataId' in fieldValues) {
        //     temp.companyDataId = fieldValues.companyDataId ? "" : "This field is required"
        // }
        // if ('userLevelId' in fieldValues) {
        //     temp.userLevelId = fieldValues.userLevelId ? "" : "This field is required"
        // }
        // if ('storeId' in fieldValues) {
        //     temp.storeId = fieldValues.storeId ? "" : "This field is required"
        // }
        // if ('uName' in fieldValues) {
        //     temp.uName = fieldValues.uName ? "" : "This field is required"
        // }
        // if ('password' in fieldValues) {
        //     temp.password = fieldValues.password ? "" : "This field is required"
        // }
        // if ('cPswd' in fieldValues) {
        //     temp.cPswd = fieldValues.cPswd ? "" : "This field is required"

        // }




        setErrors({
            ...temp
        })
        if (fieldValues == values) {
            return Object.values(temp).every(x => x == "")
        }


    }

    const [
        values,
        setValues,
        errors,
        setErrors,
        handleInputChange,
        resetForm
    ] = useForm(initialFieldValues, validate, props.setCurrentId);

    const showToast = () => {

        addToast("Deposit  saved succeffuly", { appearance: 'success' })
        addToast("Deposit  saved succeffuly", { appearance: 'success' })
        addToast("Deposit  saved succeffuly", { appearance: 'success' })
        props.fetchAllDeposit();
    }



    const handleSubmit = e => {
        e.preventDefault()


        if (validate()) {

            const onSuccess = () => {
                showToast();
                resetForm();

                if (props.openPopup == true) {
                    props.setOpenPopup(false)
                }

            }



            if (props.currentId == 0) {

                //using toast notification

                props.createDeposit({name:values.name, description:values.description,amount:parseFloat(values.amount)}, onSuccess)
                //addorEdit(values, onSuccess)

            }
            else {
                //using windows alert notification
                //props.updateDCandidate(props.currentId, values, () => {window.alert('updated')})
                //using toast notification
                props.updateDeposit(props.currentId, {name:values.name, description:values.description,amount:parseFloat(values.amount)}, onSuccess)



            }

           

        }


    }

    useEffect(() => {


        if (props.currentId != 0)
            setValues({
                ...props.depositList.find(x => x.id == props.currentId)
            })
        setErrors({})
        props.fetchAllDeposit();
        props. fetchAllCategs();
       
        // props.fetchAllUserLevel();



    }, [props.currentId])





    return (


        <section className="content">
            <div className="container-fluid">
                <div className="row">
                    {/* left column */}
                    <div className="col-md-12">
                        {/* jquery validation */}
                        <div className="card card-primary">
                            <div className="card-header">
                                <h4 className="card-title">Add Deposit <small> </small></h4>
                            </div>
                            {/* /.card-header */}
                            {/* form start */}
                            <form id="quickForm" autoComplete="off" noValidate className={classes.root} onSubmit={handleSubmit}>
                                <FormControl className={classes.formControl}
                                    {...(errors.name && { error: true })}
                                >
                                    <InputLabel >Category</InputLabel>
                                    <Select
                                        className="form-ctr"
                                        name="name"
                                        label="Category"
                                        value={values.name}
                                        onChange={handleInputChange}

                                    >
                                        <MenuItem value="">Select Category</MenuItem>
                                        {
                                            props.categoryList.map((record, index) => {
                                                return (
                                                    <MenuItem value={record.name}>{record.name}</MenuItem>

                                                )
                                            })
                                        }
                                    </Select>
                                    {errors.name && <FormHelperText>{errors.name}</FormHelperText>}
                                </FormControl>

                                <TextField className="form-ctr" name="description" label="Description" value={values.description}
                                    onChange={handleInputChange}


                                    {... (errors.description && { error: true, helperText: errors.description })}
                                />

                                <TextField className="form-ctr" name="amount" label="Amount" value={values.amount}
                                    onChange={handleInputChange}


                                    {... (errors.amount && { error: true, helperText: errors.amount })}
                                />


                                {/* <TextField className="form-ctr" name="phone" label="Categ Phone No" value={values.phone}
                                    onChange={handleInputChange}

                                    {... (errors.phone && { error: true, helperText: errors.phone })}
                                /> */}

                                {/* <TextField
                                    className="form-ctr"
                                    name="email"
                                    label="Email" type="email" value={values.email}
                                    onChange={handleInputChange}

                                    {...(errors.email && { error: true, helperText: errors.email })}
                                /> */}

                                {/* <TextField className="form-ctr" name="location" label="location" value={values.location}
                                    onChange={handleInputChange}

                                    {... (errors.location && { error: true, helperText: errors.location })}
                                />

                               
                             

                             <FormControl className={classes.formControl}
                                    {...(errors.userLevelId && { error: true })}
                                >
                                    <InputLabel >Company</InputLabel>
                                    <Select
                                        className="form-ctr"
                                        name="companyDataId"
                                        label="Company"
                                        value={values.companyDataId}
                                        onChange={handleInputChange}

                                    > */}
                                {/* <MenuItem value="">Select Company</MenuItem>
                                        <MenuItem value="1">{1}</MenuItem>
                                        {/* {
                                            props.userLevelList.map((record, index) => {
                                                return (
                                                    <MenuItem value={record.id}>{record.level}</MenuItem>

                                                )
                                            })
                                        } */}
                                {/* </Select>
                                    {errors.companyDataId && <FormHelperText>{errors.companyDataId}</FormHelperText>}
                                </FormControl>   */}




                                {/* /.card-body */}
                                <div className="card-footer">
                                    <div>
                                        <button type="submit" className="btn btn-primary" >Save</button>
                                        <button variant="contained"
                                            className="btn btn-secondary"
                                            onClick={resetForm} style={{ marginLeft: 3 }}>Reset</button>
                                    </div>

                                </div>

                            </form>

                        </div>
                        {/* /.card */}
                    </div>
                    {/*/.col (left) */}
                    {/* right column */}
                    <div className="col-md-6">
                    </div>
                    {/*/.col (right) */}
                </div>
                {/* /.row */}
            </div>{/* /.container-fluid */}
        </section>

    );
}


const mapStateToProps = state => ({

    depositList: state.deposit.list,
    expenseTypeList: state.expenseTypes.list,
    categoryList: state.category.list
})



const mapActionToProps = {
    createDeposit: DActions.createDeposit,
    fetchAllDeposit: DActions.fetchAllDeposit,
    DeleteDeposit: DActions.DeleteDeposit,
    updateDeposit: DActions.updateDeposit,
    fetchAllCategs: categActions.fetchAllCategory,
  

}
export default connect(mapStateToProps, mapActionToProps)(withStyles(styles)(AddDepositForm));